import { AskPriority, IReportData } from '../typings/types';
import { W, WN } from './constants';

//‰∏äÊä•Â∑•ÂÖ∑
type TrackerOptions = {
  logUrl: string;
};
class ReportData implements IReportData {
  private logUrl: string;
  constructor(options: TrackerOptions) {
    // console.log('‚è∞', options);
    const { logUrl } = options;
    if (logUrl) {
      this.logUrl = logUrl;
    } else {
      throw new Error('ËØ∑‰º†ÈÄíË¶ÅËÆ∞ÂΩïÊï∞ÊçÆÁöÑË∑ØÁî±~');
    }
  }
  public sendToAnalytics(level: AskPriority, body: string, uri?: string) {
    let logurl = this.logUrl;
    //‰∏¥Êó∂Êõ¥Êç¢ÂÖ∂‰ªñurl
    if (uri) {
      logurl = uri;
    }
    // console.log('Ë∑ØÁî±', logurl);
    if (level == AskPriority.URGENT) {
      if (!!W.fetch) {
        fetch(logurl, { body, method: 'POST', keepalive: true });
      } else {
        let xhr: XMLHttpRequest | null = new XMLHttpRequest();
        xhr.open('post', logurl, true);
        // ËÆæÁΩÆËØ∑Ê±ÇÂ§¥
        xhr.setRequestHeader('Content-Type', 'application/json');
        xhr.send(body); // ÂèëÈÄÅÂèÇÊï∞
        xhr.onload = function (e) {
          //   if (this.status == 200 || this.status == 304) {
          //     alert(this.responseText);
          //   }
          //ÂèäÊó∂Ê∏ÖÁêÜ‰ª•Èò≤Â§öÊ¨°ÂàõÂª∫
          xhr = null;
        };
      }
    } else if (level == AskPriority.IDLE) {
      if (!!WN.sendBeacon) {
        navigator.sendBeacon(logurl, body);
      } else {
        let img: HTMLImageElement | null = new Image();
        img.src = `${logurl}?body=${body}`;
        img.onload = function () {
          //ÁªüËÆ°ÂÆåÊàêÊî∂ÂõûÂàõÂª∫ÁöÑÂÖÉÁ¥†Èò≤Ê≠¢ÂÜÖÂ≠òÊ≥ÑÈú≤
          img = null;
        };
      }
    }
  }
}
export default ReportData;
